    background: #f3f3f3;
}

.Header_main_Menu__3tOwN li {
    display: inline-block;
}

.Header_main_Menu__3tOwN li:after {
    content: "|";
    color: #606060;
    display: inline-block;
    vertical-align: top;
}

.Header_main_Menu__3tOwN li:last-child:after {
    content: none;
}

.Header_main_Menu__3tOwN a {
    text-decoration: none;
    font-family: 'Ubuntu Condensed', sans-serif;
    letter-spacing: 2px;
    position: relative;
    padding-bottom: 10px;
    margin: 0 34px 0 30px;
    font-size: 17px;
    text-transform: uppercase;
    display: inline-block;
    transition: color .2s;
}

