    color: #9d999d;
}

.Header_main_Menu__3tOwN a.Header_active__2Kglr {
    border-bottom: #feb386 3px solid;
    color: #feb386;
}

.Header_main_Menu__3tOwN a.Header_current__2dXry, .Header_main_Menu__3tOwN a:hover {
    color: #feb386;
}

.Header_main_Menu__3tOwN a:before,
.Header_main_Menu__3tOwN a:after {
    content: "";
    position: absolute;
    height: 4px;
    top: auto;
    right: 50%;
    bottom: -5px;
    left: 50%;
    background: #feb386;
}

